.subscribe{
  position: relative;
  &__inner{
    min-height: calc(100 * var(--vh));
    display: grid;
    grid-template-columns: 50% 50%;
  }
  &__img{
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 2;
    width: 31.9375em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.subscribe-group{
  padding: 3.5em 3.5em 3em 10.5em;
  @media screen and (min-width: 577px){
    &:nth-child(even){
      .program{
        order: -1;
      }
    }
    &:nth-child(odd){
      border-right: .5em solid #fff;
    }
    &:nth-child(even){
      border-left: .5em solid #fff;
    }
    &:nth-child(1),
    &:nth-child(2){
      border-bottom: .5em solid #fff;
    }
    &:nth-child(3),
    &:nth-child(4){
      border-top: .5em solid #fff;
    }
    &:nth-child(4){
      .subscribe-group__top{
        padding-left: 20em;
      }
    }
  }
  &__top{
    max-width: 39.5em;
    .gradient-pink-text{
      display: inline-block;
      margin-left: .15em;
    }
  }
  &__grid{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
    max-width: 39.5em;
  }
}

.program,
.functions{
  &__items{
    margin-top: .625em;
  }
}

.program-item,
.function-item{
  position: relative;
  &:after,
  &:before{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background: linear-gradient(180deg, #5D3EFE 0%, #FFC208 100%);
  }
  &:after{
    display: none;
    bottom: unset;
    top: 0;
  }
  &:first-child{
    &:after{
      display: block;
    }
  }
}

.function-item{
  padding: .375em 0;
  &.slideToggle-block--active{
    .function-item__btn{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
  &__show{
    display: flex;
    justify-content: space-between;
    cursor: pointer;
  }
  &__title{
    font-family: $font2;
    span{
      font-size: .8125em;
      line-height: 108%;
    }
  }
  &__btn{
    width: .875em;
    height: .875em;
    position: relative;
    span{
      @include centerAbsolute;
      transition: all .3s;
    }
    ._view2{
      opacity: 0;
    }
  }
  &__wrap{
    padding-top: .25em;
    .text12{
      line-height: 117%;
      p{
        line-height: inherit;
      }
    }
  }
}

.program-item{
  padding: .25em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__title,
  &__desc{
    font-size: .8125em;
    line-height: 108%;
    font-family: $font2;
  }
  &__desc{
    min-width: 4.2em;
    text-align: right;
  }
}


@media screen and (max-width: 576px){
  .subscribe{
    padding: 2.5em 1em;
    &__inner{
      grid-template-columns: 100%;
      margin-top: 5.25em;
      gap: 1em;
    }
    &__img{
      position: static;
      width: 100%;
      display: block;
      transform: unset;
    }
  }

  .subscribe-group{
    padding: 1.5em;
    @include setBackdrop;
    &__top{
      display: flex;
      justify-content: space-between;
      .text32{
        font-size: 1.625em;
      }
      .gradient-pink-text{
        font-size: 1.75em;
      }
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }
}
