@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.round-btn{
    width: 4em;
    height: 4em;
    border-radius: 50%;
    img,
    svg{
        width: 2em;
        height: auto;
    }
}

.m-btn-white{
    background: rgba(255, 255, 255, 0.40);
    backdrop-filter: blur(10px);
    border: 1px solid #fff;
    &:hover{
        background: rgba(255, 255, 255, 0.70);
    }
}

.m-btn-pink{
    background: #F1E4F8;
    backdrop-filter: blur(10px);
    border: 1px solid #fff;
    &:hover{
        background: darken(#F1E4F8,5%);
    }
}

.m-btn-purple{
    background: $purple2;
    color: #fff;
    height: 4em;
    padding: 0 2em;
    border-radius: 1em;
    font-weight: 700;
    &._linear{
        border-radius: 1rem;
        background: linear-gradient(90deg, #5D3EFE 0%, #C64DFF 100%);
        background-position: center;
        &:hover{
            background: linear-gradient(90deg, #5D3EFE 0%, #C64DFF 100%);
            background-size: 250%;
        }
    }
    &:hover{
        background: darken($purple2,10%);
        color: #fff;
    }
    span{
        font-size: 1em;
        line-height: 120%;
    }
    &._small{
        padding: 0 1em;
        height: 2.25em;
        border-radius: 6.25em;
        font-weight: 400;
    }
}

.m-btn-purple-outline{
    border: 1px solid $purple2;
    color: $purple2;
    &:hover{
        background-color: $purple2;
        color: #fff;
    }
}


@media screen and (max-width: 576px){
    .m-btn-purple{
        height: 3.25em;
        span{
            font-size: 1em;
        }
    }
}
